body{
    margin:0;
    background-image: linear-gradient(to right, #207a2f, #429430, #66ad2e, #8cc628, #b6df1e, #d0db06, #e8d700, #ffd100, #ffaf00, #ff8b04, #ff6522, #ff3636);
}

#imgContainer{
    width: 100%;
    display: grid;
    grid-template-columns:repeat(6,300px);
    grid-template-rows:repeat(2,300px);
    margin-bottom: 60px;
}

.gamePic{
    margin: 50px;
    width: 200px;
    height: 250px;
    border-radius: 20%;
    box-shadow: 0px 0px 20px black;
}


#scoreContainer{
   float:right;
   text-align: center;
   font-size: 30px;
}


.gamePic:hover{
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
    box-shadow: 0px 0px 70px yellow;
  }

  .header {
      top:0;
    padding: 15px;
    text-align: center;
    background: #1abc35;
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 20px;
    color: yellow;

  }